.Streams-grid {
    position: absolute;
    display: grid;
    grid-template-columns: auto auto 1fr;
    grid-template-rows: 65px;
    align-items: center;
    column-gap: 20px;
    row-gap: 40px;
    width: 80%;
    top: 50px;
}

.Streams-header {
    align-self: end;
    justify-self: left;
    font-size: 45px;
    margin-right: 7px;
}

.Streams-separator {
    transition: 0.2s;
    border-radius: 3px;
    width: 3px;
    height: 100%;
}

.Streams-separator.light {
    background-color: var(--strong-dark);
}

.Streams-separator.dark {
    background-color: var(--strong-light);
}

.button {
    align-self: start;
    justify-self: left;
    font-size: 20px;
}

.Streams-refresh {
    height: 25px;
    width: 25px;
    transition: 0.2s;
}

.Streams-refresh.rotating {
    animation-name: rotation;
    animation-duration: 1s;
    animation-iteration-count: infinite;
}

.Streams-refresh:hover {
    cursor: pointer;
}

.Streams-refresh.light {
    filter: invert(0%) sepia(89%) saturate(23%) hue-rotate(124deg) brightness(101%) contrast(75%); /* strong dark */
}

.Streams-refresh.dark {
    filter: invert(99%) sepia(1%) saturate(2018%) hue-rotate(250deg) brightness(109%) contrast(63%); /* strong light */
}

@keyframes rotation {
    0%   {transform: rotate(0deg);}
    100% {transform: rotate(360deg);}
  }

.Streams-available {
    grid-column: 1 / 4;
    display: grid;
    grid-template-columns: auto 1fr;
    gap: 10px;
    align-items: center;
}

.Streams-available-grid {
    grid-column: 1 / 4;
    display: grid;
    grid-template-columns: 1fr;
    grid-auto-rows: 50px;
    align-items: center;
    justify-items: center;
    gap: 5px;
}
